import React from 'react';
import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import FormControl from '@material-ui/core/FormControl';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import MakesModels from '../data/makesModels.json';

const styles = theme => ({
    root: {
        ...theme.mixins.gutters(),
        padding: theme.spacing.unit * 2,
    },
});

class Model extends React.Component {  
    state = {
      year: '',
      newOrPreowned: sessionStorage.getItem("newOrPreowned"),
      dieselOrGas: sessionStorage.getItem("dieselOrGas"),
      manufacturer: sessionStorage.getItem("manufacturer")
    };

    handleChangeYear = event => {
        this.setState({ year: event.target.value });
        sessionStorage.setItem("year", event.target.value)
    };

    render() {
        const { classes } = this.props;
        return (
        <Paper className={classes.root} elevation={1}>
            <Typography component={'span'} variant="h5" component="h3">
                Select your Model...'
            </Typography>
            
        
            <FormControl component="fieldset">
                <RadioGroup
                aria-label="position"
                name="position"
                value={this.state.value}
                onChange={this.handleChangeYear}
                row
                >
                <FormControlLabel
                value="2019"
                control={<Radio color="primary" />}
                label="2019"
                labelPlacement="start"
                />          
                <FormControlLabel
                    value="2018"
                    control={<Radio color="primary" />}
                    label="2018"
                    labelPlacement="start"
                />
                </RadioGroup>
            </FormControl>
        </Paper>
        );
    }
    
}
Model.propTypes = {
    classes: PropTypes.object.isRequired,
};
export default withStyles(styles)(Model);